import jsPDF from 'jspdf';

export default class ARReceiptGenerator {

    capitalizeWords(str){
        return str.replace(/\w\S*/g, function(txt){return txt.charAt(0).toUpperCase() + txt.substr(1).toLowerCase();});
    }

    amountInWords(amount){
        var thousands = ['', 'thousand', 'million', 'billion', 'trillion'];
        var ones = ['zero', 'one', 'two', 'three', 'four', 'five', 'six', 'seven', 'eight', 'nine'];
        var teens = ['ten', 'eleven', 'twelve', 'thirteen', 'fourteen', 'fifteen', 'sixteen', 'seventeen', 'eighteen', 'nineteen'];
        var tens = ['twenty', 'thirty', 'forty', 'fifty', 'sixty', 'seventy', 'eighty', 'ninety'];

        amount = amount.toString();
        // amount = amount.replace(/[\, ]/g, '');
        if (amount != parseFloat(amount))
            return "Zero ";
        var decPointIdx = amount.indexOf('.');
        if (decPointIdx == -1)
            decPointIdx = amount.length;
        if (decPointIdx > 15)
            return 'too big';
        var amtVal = amount.split('');
        var strValue = '';
        var statusVal = 0;
        for (var i = 0; i < decPointIdx; i++) {
            if ((decPointIdx - i) % 3 == 2) {
                if (amtVal[i] == '1') {
                    strValue += teens[Number(amtVal[i + 1])] + ' ';
                    i++;
                    statusVal = 1;
                } else if (amtVal[i] != 0) {
                    strValue += tens[amtVal[i] - 2] + ' ';
                    statusVal = 1;
                }
            } else if (amtVal[i] != 0) {
                strValue += ones[amtVal[i]] + ' ';
                if ((decPointIdx - i) % 3 == 0)
                    strValue += 'hundred ';
                statusVal = 1;
            }
            if ((decPointIdx - i) % 3 == 1) {
                if (statusVal)
                    strValue += thousands[(decPointIdx - i - 1) / 3] + ' ';
                statusVal = 0;
            }
        }
        if (decPointIdx != amount.length) {
            var amountLength = amount.length;
            strValue += 'and ';
            for (var j = decPointIdx + 1; j < amountLength; j++)
                strValue += ones[amtVal[j]] + ' ';
        }
        return strValue.replace(/\s+/g, ' ');
    }

    generatePDF(pdfData) {
        Date.prototype.toShortFormat = function () {
            let monthNames = [
                "January",
                "February",
                "March",
                "April",
                "May",
                "June",
                "July",
                "August",
                "September",
                "October",
                "November",
                "December",
            ];
            let day = this.getDate();
            let monthIndex = this.getMonth();
            let monthName = monthNames[monthIndex];
            let year = this.getFullYear();
            return `${monthName} ${day}, ${year}`;
        };

        const doc = new jsPDF("p", "mm", "a4");

        let padding = 0;

        // for(const pdfInput of pdfData){
        for (let i = 0; i < pdfData.length; i++) {
            let pdfInput = pdfData[i];
            let rate = +(pdfInput.rate);
            let computedAmt = (pdfInput.totalNoOfDays * rate);

            doc.addImage("data:image/png;base64,", 
            "PNG", 16, 16 + padding);
            doc.setFontSize(12);
            doc.setFontStyle("bold");
            doc.text("IBA BOTANICALS INC.", 60, 24 + padding);
            
            doc.setFontSize(10);
            doc.setFontStyle("bold");
            doc.setTextColor(128, 0, 128);
            doc.text("ACKNOWLEDGMENT RECEIPT", 129, 26.5 + padding);

            doc.setTextColor("black");

            doc.setFont("times");
            doc.setFontSize(11);
            doc.setFontStyle("bold");
            doc.text("Payee Name:", 17, 40 + padding);
            doc.text("Date: ", 118, 40 + padding);
            doc.setFont("helvetica");
            doc.setFontStyle("normal");
            doc.text(pdfInput.name, 45, 40 + padding);
            const today = new Date();
            doc.text(today.toShortFormat(), 160, 40 + padding);

            doc.setFont("courier");
            doc.setFontSize(10);
            doc.setFontStyle("bold");
            doc.text("P A R T I C U L A R S (Pls provide breakdown)", 17, 48 + padding);
            doc.text("C. Ctr.", 120, 48 + padding);
            doc.text("A M O U N T S", 163, 48 + padding);

            doc.setFont("helvetica");
            doc.setFontSize(11);
            doc.setFontStyle("normal");
            doc.text(pdfInput.totalNoOfDays.toString() +  " day/s", 50, 60 + padding);
            doc.text(rate.toLocaleString("en-US", {minimumFractionDigits: 2}), 80, 60 + padding);
            // doc.setFont("calibri");
            doc.setFontSize(11);

            let computedAmtFmt = computedAmt.toLocaleString("en-US", {minimumFractionDigits: 2});
            if(computedAmtFmt.length > 10){
                doc.text(computedAmtFmt, 171.5, 60 + padding);
            }
            else{
                doc.text(computedAmtFmt, 173, 60 + padding);
            }

            // doc.setFont("Arial");
            doc.setFontSize(10);
            doc.text(pdfInput.activity, 17, 65 + padding);

            if(pdfInput.remarks.length > 16){
                doc.text(pdfInput.remarks.slice(0, 16), 80, 65 + padding);
                doc.text(pdfInput.remarks.slice(15), 80, 70 + padding);
            }else{
                doc.text(pdfInput.remarks, 80, 65 + padding);
            }

            doc.text(pdfInput.area, 17, 70 + padding);

            doc.setFont("times");
            doc.setFontSize(9);
            doc.setFontStyle("normal");
            doc.text("Amount in Words:", 17, 76 + padding);
            doc.text("Signature", 120, 76 + padding);

            doc.setFont("helvetica");
            doc.setFontSize(11);
            doc.text("PHP", 160, 76 + padding);

            doc.setFontStyle("bold");

            if(computedAmtFmt.length > 10){
                doc.text(computedAmtFmt, 171.5, 76 + padding);
            }
            else{
                doc.text(computedAmtFmt, 173, 76 + padding);
            }

            // doc.setFont("Arial");
            doc.setFontSize(10);
            doc.setFontStyle("normal");

            let computedAmtFmts = computedAmt.toFixed(2).split(".");

            let amtInWrdsWhole = this.capitalizeWords(this.amountInWords(+(computedAmtFmts[0])).trim());
            let amtInWrdsPart = this.capitalizeWords(this.amountInWords(+(computedAmtFmts[1])).trim());
            let amtInWrds = "";

            if(amtInWrdsPart){
                amtInWrds = amtInWrdsWhole + " Pesos And " + amtInWrdsPart+ " Centavos Only";
            }
            else{
                amtInWrds = amtInWrdsWhole + " Pesos Only";
            }
            
            if(amtInWrds.length > 50){
                let spaceLastIdx = amtInWrds.lastIndexOf(" ");
                let amtInWrdsTemp = amtInWrds;
                while(spaceLastIdx > 50){
                    amtInWrdsTemp = amtInWrdsTemp.slice(0, spaceLastIdx).trim();
                    spaceLastIdx = amtInWrdsTemp.lastIndexOf(" ");
                }
                doc.text(amtInWrds.slice(0, spaceLastIdx + 1), 20, 82 + padding);
                doc.text(amtInWrds.slice(spaceLastIdx, amtInWrds.length).trim() , 20, 90 + padding);
            }else{
                doc.text(amtInWrds, 20, 82 + padding);
            }

            // doc.setFont("Calibri");
            doc.setFontSize(9);
            doc.setFontStyle("bold");
            doc.text(pdfInput.cano, 173, 90 + padding);

            doc.setLineWidth(0.5);
            //Big rect
            doc.rect(15, 15 + padding, 180, 70);
            //AR Text Rect
            doc.rect(118, 20 + padding, 77, 10);

            //Name horizontal line
            doc.line(45, 41 + padding, 100, 41 + padding);
            //Date horizontal line
            doc.line(158, 41 + padding, 195, 41 + padding);

            //1st horiontal line within rect
            doc.line(15, 44 + padding, 195, 44 + padding);
            //2nd horiontal line within rect
            doc.line(15, 49.5 + padding, 195, 49.5 + padding);
            //3rd horiontal line within rect
            doc.line(15, 72 + padding, 195, 72 + padding);

            //1st Vertical line within rect
            doc.line(118, 44 + padding, 118, 85 + padding);
            //2nd Vertical line within rect
            doc.line(158, 44 + padding, 158, 85 + padding);
            //3rd Vertical line within rect
            doc.line(170, 49 + padding, 170, 85 + padding);

            if((i + 1) % 3 === 0){
                if((i + 1) != pdfData.length){
                    doc.addPage("a4", "p");
                }
                padding = 0;
            }
            else{
                //Dashed line
                doc.setLineWidth(0.1);
                doc.setLineDash([2.5]);
                doc.line(1, 99.5 + padding, 209, 99.5 + padding);
                doc.setLineWidth(0.5);
                doc.setLineDash([]);
                //Add padding
                padding += 99;
            }

        }

        //End
        doc.save('AR-' + Date.now() + '.pdf')
    }
}